import s from './PanelType.module.css';
import { NavLink } from 'react-router-dom';

const PanelType = () => {
	let btnMap = [
		{ title: ' ', path: 'readers' },
		{ title: ' ', path: 'books' },
		{ title: '', path: 'applications' },
		{ title: '', path: 'activities' },
		{ title: '  ', path: 'event-reporting' },
		{ title: ' ', path: 'annual-reporting' },
		{ title: '', path: 'invertory' },
		{ title: '', path: 'librarians' },
		{ title: '', path: 'affiliates' },
		{ title: '', path: 'news' },
		{ title: '  ', path: 'reports-to-the-news' },
		{ title: '', path: 'visitors' },
	];

	let btnMapAdd = btnMap.map(b => (
		<NavLink
			to={'/admin/' + b.path}
			className={navData =>
				(navData.isActive ? s.isActive : s.notActive) + ' ' + s.item
			}
		>
			{b.title}
		</NavLink>
	));

	return (
		<div className={s.container}>
			<div className={s.inner}>{btnMapAdd}</div>
		</div>
	);
};

export default PanelType;
